@use "abstracts/mixins/emit-css-vars" as *
@use "abstracts/variables" as *
@use "themes" as *

/* Global tokens */
@include emit-css-vars($global-tokens)

/* Sidebar */
@include emit-css-vars($sidebar, $prefix: "sidebar")

/* Page wrapper offsets */
@include emit-css-vars($page-wrapper-offsets, $prefix: "wrapper-offset")

/* Typography */
@include emit-css-vars($typography)
@include emit-css-vars($font-weights, $prefix: "font-weight")

/* Border radii and widths */
@include emit-css-vars($border-radii, $prefix: "border")
@include emit-css-vars($border-widths, $prefix: "border")

/* Motion */
@include emit-css-vars($motion-durations, $prefix: "motion")
@include emit-css-vars($motion-easings, $prefix: "motion")

/* Themes */
@include emit-css-vars($light, "[data-theme='light']")
@include emit-css-vars($dark, "[data-theme='dark']")
