.highlight
  border-radius: var(--border-radius-default)
  overflow-x: auto
  border: var(--border-width-default) solid var(--color-border-muted)

  .c, .cm, .c1
    color: var(--color-code-syntax-comment) /* Comment */
    font-style: italic

  .cp, .cs
    color: var(--color-code-syntax-comment)
    font-style: italic
    font-weight: var(--font-weight-bold)

  .err
    color: var(--color-code-syntax-error-text) /* Error */
    background-color: var(--color-code-syntax-error-bg)

  .k, .kc, .kd, .kn, .kp, .kr, .ow
    color: var(--color-code-syntax-keyword) /* Keyword */
    font-weight: var(--font-weight-bold)

  .kt
    color: var(--color-code-syntax-namespace) /* Type / Namespace */
    font-weight: var(--font-weight-bold)

  .m, .mf, .mh, .mi, .mo, .il
    color: var(--color-code-syntax-number) /* Number */

  .s, .sb, .sc, .sd, .s2, .se, .sh, .si, .sx, .sr, .s1
    color: var(--color-code-syntax-string) /* String */

  .ss
    color: var(--color-code-syntax-symbol) /* Symbol */

  .na, .no, .nv, .vc, .vg, .vi
    color: var(--color-code-syntax-name) /* Name/Variable */

  .nb, .bp
    color: var(--color-code-syntax-builtin) /* Builtin */

  .nc
    color: var(--color-code-syntax-namespace) /* Class */
    font-weight: var(--font-weight-bold)

  .nd
    color: var(--color-code-syntax-decorator) /* Decorator */
    font-weight: var(--font-weight-bold)

  .ni, .nn
    color: var(--color-code-syntax-entity) /* Entity */

  .ne, .nf, .nl
    color: var(--color-code-syntax-function) /* Function */
    font-weight: var(--font-weight-bold)

  .nt
    color: var(--color-code-syntax-tag) /* Tag */

  .gu
    color: var(--color-code-syntax-heading) /* Subheading */

  .gd
    color: var(--color-code-syntax-deleted-text) /* Deleted */
    background-color: var(--color-code-syntax-deleted-bg)

  .gi
    color: var(--color-code-syntax-inserted-text) /* Inserted */
    background-color: var(--color-code-syntax-inserted-bg)

  .go
    color: var(--color-code-syntax-output) /* Output */

  .gp
    color: var(--color-code-syntax-meta) /* Prompt */

  .gs
    font-weight: var(--font-weight-bold) /* Strong */

  .ge
    font-style: italic /* Emphasis */

  .w
    color: var(--color-code-syntax-subtle) /* Whitespace */
