@use "abstracts/mixins/transition-properties" as *

.badge
  display: inline-block
  padding: 0.25rem 0.35rem
  font-size: clamp(0.6875rem, 0.9vw, 0.75rem)
  font-weight: var(--font-weight-medium)
  line-height: 1.2
  text-align: center
  white-space: nowrap
  vertical-align: middle
  background-color: var(--color-bg-muted)
  color: var(--color-fg-muted)
  border: var(--border-width-default) solid var(--color-border-muted)
  border-radius: var(--border-radius-default)

  @include transition-properties((background-color, color), $duration: var(--motion-duration-fast), $easing: var(--motion-easing-in-out))
